.signin {
    position: relative;
    background-image: url(./background_signin.jpg);
    background-repeat: no-repeat;
    background-size: 100%;
    justify-content: center;
    height: 100vh;
}

.home {
    text-decoration: none;
    font-size: 2rem;
    color: #2197fe;
    padding: 16px 20px;
}

.form {
    position: absolute;
    top: 80px;
    left: 300px;
    width: 40%;
    margin-top: 0;
    background-color: #f8f8f8;
    border-radius: 0.2rem;
    box-shadow: 1px 1px #dbdada;
}

.form_div {
    display: flex;
    flex-direction: column;
    margin: 0.6rem;
}

.form_div_h1 {
    text-align: center;
}

.form_label {
    margin: 0.5rem 0;
    display: block;
}

.form_input,
.form_btn {
    padding: 0.5rem;
    border-radius: 0.3rem;
    border: 0.1rem #a4a4a4 solid;
    font-size: 1.2rem;
    font-family: Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.form_btn {
    background-color: #f8f8f8;
    cursor: pointer;
}

.form_input:hover,
.form_btn:hover {
    border: 0.1rem #404040 solid;
}

.form_btn.primary {
    background-color: #1c8af1;
    color: #fff;
}

.form_btn_block {
    width: 100%;
}

.form_button.small {
    font-size: 1.2rem;
}

.form_div.center {
    padding-bottom: 10px;
    text-align: center;
}

.legend {
    color: rgb(0, 0, 0);
    position: relative;
    bottom: 10px;
    font-weight: 500;
    font-size: 20px;
}

.radio {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    margin-right: 20px;
    font-weight: 500;
}

.radio__input {
    display: none;
}

.radio__radio {
    width: 1.25em;
    height: 1.25em;
    border: 2px solid #383838;
    border-radius: 50%;
    margin-right: 4px;
    box-sizing: border-box;
    padding: 2px;
}

.radio__radio::after {
    content: "";
    width: 100%;
    height: 100%;
    display: block;
    background: #2197fe;
    border-radius: 50%;
    transform: scale(0);

    transition: transform 0.15s;
}

.radio__input:checked + .radio__radio::after {
    transform: scale(1);
}

.custome_input_password {
    display: flex;
    flex-direction: column;
    position: relative;
}

.far_eye {
    position: absolute;
    top: 25%;
    right: 2%;
    font-size: 20px;
    cursor: pointer;
}

.far_eye_slash {
    position: absolute;
    top: 25%;
    right: 2%;
    font-size: 20px;
    cursor: pointer;
}
