.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100px;
    background-image: linear-gradient(0, #4da8f8, #1a94ff);
    z-index: 100;
}

.navbar {
    height: 40px;
    display: flex;
    justify-content: space-between;
}

.navbar_home {
    font-size: 1.8rem;
    text-decoration: none;
    color: white;
    padding: 5px 30px;
}

.navbar_list {
    height: 100%;
    margin: 0;
    padding-right: 20px;
    list-style: none;
    justify-content: space-around;
    z-index: 200;
}

.navbar_item {
    margin-top: 10px;
    padding: 0px 5px;
    text-decoration: none;
    display: inline-block;
    font-size: 1.1rem;
    font-weight: 400;
    color: white;
}

.navbar_item_separate {
    border-right: 1px ridge rgb(199, 196, 196);
}

/* Header with search */
.list_search_cart {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.search {
    display: flex;
    margin-left: 150px;
    width: 850px;
    height: 40px;
    border-radius: 2px;
    background-color: white;
    align-items: center;
}

.search_input {
    flex: 1;
    height: 100%;
    border: none;
    outline: none;
    font-size: 1.1rem;
    padding: 0 16px;
    border-radius: 3px;
}

.search_btn {
    background-color: #1a94ff;
    border: none;
    width: 60px;
    height: 34px;
    outline: none;
    margin-right: 3px;
    border-radius: 3px;
}

.search_btn:hover {
    cursor: pointer;
    opacity: 0.9;
}

.search_btn_icon {
    color: white;
    font-size: 1.1rem;
}
.main_search{
    width: 100%;
    height:calc(100vh - 100px);
    position: absolute;
    top:100px;
    left: 0;
    background-color: white;
    overflow: scroll;
    padding: 20px 60px;
}
