.footer {
    background-image: linear-gradient(0, rgb(63, 62, 62), rgb(36, 36, 36));
    color: white;
    border-top: 4px solid #1a94ff;
    position: absolute;
    width: 100%;
    height: 96px;
}

.footer_row {
    height: 40px;
    margin-left: 43%;
    align-items: center;
    display: flex;
}

.footer_content {
    margin: 10px;
    text-align: center;
}

.phone_icon {
    font-size: 1.3rem;
    margin-right: 5px;
}